body {
  margin: 0;
}

.login-container {
  width: 100%;
  height: 100vh;
  background-color: #000;
  display: flex;
  overflow: hidden;
}

.login-image {
  width: 100%;
  max-width: 1024px;
  height: 100%;
  filter: grayscale();
  background: rgba(211, 211, 211, .5) url("onepm.67bbf497.png") 50% / cover no-repeat;
  border-radius: 30px 0 0 30px;
}

.login-form-container {
  width: 100%;
  max-width: 612px;
  color: #fcfcfc;
  flex-direction: column;
  align-items: center;
  margin: 44px;
  display: flex;
}

.signin {
  color: #fcfcfc;
  letter-spacing: .02em;
  width: 210px;
  white-space: nowrap;
  border-right: 3px solid;
  margin-top: 0;
  margin-bottom: 36px;
  font-family: Roboto;
  font-size: 64px;
  font-weight: 500;
  animation: typing .8s steps(8, end) forwards, blinking 1.5s infinite;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 210px;
  }
}

@keyframes blinking {
  0% {
    border-right-color: rgba(0, 0, 0, 0);
  }

  50% {
    border-right-color: #fff;
  }

  100% {
    border-right-color: rgba(0, 0, 0, 0);
  }
}

.form-decor {
  flex-direction: column;
  display: flex;
}

.username-label {
  letter-spacing: .02em;
  width: 0;
  white-space: nowrap;
  margin-bottom: 10px;
  margin-left: 10px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  animation: typing-label .8s steps(9, end) .9s forwards;
  overflow: hidden;
}

@keyframes typing-label {
  from {
    width: 0;
  }

  to {
    width: 125px;
  }
}

.username-input {
  width: 400px;
  height: 50px;
  text-align: center;
  color: #fff;
  background: #302d2d;
  border: none;
  border-radius: 5px;
  font-size: 22px;
}

.username-input:focus {
  outline-offset: .2em;
  outline: 2px dashed #ad2b89;
}

.password-label {
  letter-spacing: .02em;
  width: 0;
  white-space: nowrap;
  margin-bottom: 10px;
  margin-left: 10px;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  animation: typing-label-password .8s steps(8, end) 1.9s forwards;
  overflow: hidden;
}

@keyframes typing-label-password {
  from {
    width: 0;
  }

  to {
    width: 115px;
  }
}

.password-input {
  width: 400px;
  height: 50px;
  text-align: center;
  color: #fff;
  background: #302d2d;
  border: none;
  border-radius: 3px;
  font-size: 22px;
}

.password-input:focus {
  outline-offset: .2em;
  outline: 2px dashed #ad2b89;
}

.form-button-signin {
  width: 410px;
  height: 87px;
  color: #c4c4c4;
  letter-spacing: .02em;
  background: none;
  border: 4px solid #bd00ff;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 30px;
  transition: background-color .3s, color .3s;
}

.form-button-signin:hover {
  color: #fff;
  background-color: #bd00ff;
}

.form-button-signin:focus {
  color: #fff;
  background-color: #bd00ff;
  outline: none;
}

.login-text {
  color: #c4c4c4;
  letter-spacing: .02em;
  font-family: Roboto;
  font-size: 24px;
}

.login-link {
  color: #bd00ff;
  margin-left: 11px;
  text-decoration: none;
}

.login-link:focus, .login-link:hover {
  color: #ad2b89;
}

